
let repositorio;

const repos = []
await gitApi()
export default  function Coding(){
    
    return(
        <div className="coding_body"> 
            <ul className="repos">
                {repos.filter(l=>{return l[1]!= null}).map(list=>{
                
                    return <li><span>{list[0]}</span>
                                <p>{list[1]}</p>
                            </li>
                })}
            </ul>
        </div>
    )
}


async function gitApi(){
 const url = "https://api.github.com/users/vitaminanescau/repos" 
 await fetch(url)
            .then(res=>{return res.json()})
            .then(p=>{
                repositorio = p
                repositorio.forEach(repo=>{
                     repos.push([repo.name,repo.description,repo.language,])
                })
            })
        }
        
          
          
          